import { FC, useState } from 'react'
import { Button, Card, Divider, Form, Image, Input, Radio, Slider, Space, Upload } from 'antd'
import { RcFile } from 'antd/es/upload'
import { UploadOutlined } from '@ant-design/icons'

import { Config, iconPrefix, jobs } from '../common'

interface OptionsProps {
  config: Config
  setConfig: (config: Config) => void
}

const Options: FC<OptionsProps> = ({ config, setConfig }) => {
  const [key, setKey] = useState('basic')

  const components = {
    basic: (
      <Space direction="vertical">
        <Form.Item label=" ">
          <Upload
            maxCount={1}
            listType="picture"
            beforeUpload={() => false}
            onRemove={() => setConfig({ ...config, image: null })}
            onChange={(info) => {
              if (info.fileList.length) {
                const reader = new FileReader()
                reader.addEventListener('load', () => setConfig({ ...config, image: reader.result }))
                reader.readAsDataURL(info.file as RcFile)
              }
            }}
          >
            <Button icon={<UploadOutlined />}> </Button>
          </Upload>
        </Form.Item>

        <Form.Item label="  /">
          <Slider
            min={0}
            max={2}
            step={0.01}
            tooltip={{ formatter: (value) => `${Math.floor((value ?? 0) * 100)}%` }}
            defaultValue={config.scale}
            onChange={(scale) => setConfig({ ...config, scale })}
          />
        </Form.Item>

        <Form.Item label="">
          {jobs.map((job, index) =>
            job ? (
              <Image
                key={job}
                width="1.5rem"
                preview={false}
                alt={job}
                src={`${iconPrefix}/${job}.png`}
                onClick={() => setConfig({ ...config, job })}
              />
            ) : (
              <Divider key={index} type="vertical" />
            )
          )}
        </Form.Item>

        <Form.Item label=" ">
          <Space.Compact>
            <Input
              value={config.name}
              onChange={(e) => setConfig({ ...config, name: e.target.value })}
              placeholder=""
            />
            <Input
              value={config.server}
              onChange={(e) => setConfig({ ...config, server: e.target.value })}
              placeholder=""
              prefix="@"
            />
            <Input
              value={config.title}
              onChange={(e) => setConfig({ ...config, title: e.target.value })}
              placeholder=""
            />
          </Space.Compact>
        </Form.Item>
      </Space>
    ),

    theme: (
      <Space direction="vertical">
        <Form.Item label=" ">
          <Radio.Group
            value={config.color}
            onChange={(color) =>
              setConfig({
                ...config,
                color: color.target.value as Config['color'],
              })
            }
            defaultValue="black"
          >
            <Radio value="black"></Radio>
            <Radio value="white"></Radio>
          </Radio.Group>
        </Form.Item>
        <Form.Item label=" ">
          <Input
            placeholder="  ."
            value={config.font}
            onChange={(e) => setConfig({ ...config, font: e.target.value })}
          />
        </Form.Item>
      </Space>
    ),
  }

  const tabs: { key: keyof typeof components; tab: string }[] = [
    { key: 'basic', tab: '' },
    { key: 'theme', tab: '' },
  ]

  return (
    <Card style={{ marginTop: 10 }} tabList={tabs} activeTabKey={key} onTabChange={setKey}>
      <Form layout="vertical">{components[key as keyof typeof components]}</Form>
    </Card>
  )
}

export default Options
